.btn {
  background-color: #757575;
  background-color: var(--btn-bg);
  border: none;
  padding: 0.5rem 1rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #32cd32;
  color: var(--lime-green);
  font-size: 2rem;
  font-weight: 600;
  cursor: pointer;
}

.btn span {
  color: #fff;
  font-size: 1.4rem;
  font-weight: normal;
}

.bin_btn {
  position: absolute;
  top: 70%;
  left: 57%;
  -webkit-transform: translate(-70%, -50%);
          transform: translate(-70%, -50%);
}

.clinker_btn {
