  left: 0;
  background-color: #32cd32;
  background-color: var(--lime-green);
  border: none;
  padding: 0.5rem 1rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 1.6rem;
  /* cursor: pointer; */
}

.ds_btn-2 {
